@import "../../scss/variables";

.location-text {
  text-align: center;
  margin: 2rem 0;
  &-address,
  &-phone {
    &:hover {
      span {
        transition: color 0.2s;
        color: $secondary-color;
      }
    }
  }
  &-address span:last-child {
    margin-bottom: 0.5rem;
  }
  span {
    display: block;
  }
}
