@import "../../scss/variables";

#section-image {
  margin-bottom: 1rem;
}
.section-img {
  height: $page-img-height;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: relative;
  //   visibility: hidden;
  //   animation: fadeIn2 800ms forwards ease-in;

  .overlay {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    &-light {
      background: $gradient-transparency;
      opacity: 0.5;
    }
    &-dark {
      background: $overlay-color;
      background: $overlay-color;
      opacity: 0.3;
    }
  }
}

.overlay-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

