@import "../../scss//variables";
.location-page {
  display: block;
  // animation: fadeIn2 800ms forwards ease-in;
}

#location-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 2rem;

  // Location & Phone
  .location-text {
    font-size: 1.2rem;
    span {
      display: block;
    }
    a,
    a:hover,
    a:visited,
    a:active {
      text-decoration: underline;
    }
    a:link,
    a:visited {
      color: $primary-color;
    }
    a:hover {
      color: $secondary-color;
    }
  }
}

#location-map {
  position: relative;
  width: 100%;
  height: $map-height;
}
