.carousel {
  // height: 100%;
  // &-inner,
  // &-item {
  //   height: 100%;
  // }
  &-item {
    .main-slide-img {
      width: 100%;
      height: $max-height;
      position: relative;
      background-repeat: no-repeat;
      background-position: 50% center;
      background-size: cover;
      // animation: cameraAngleChange 3000ms forwards ease-in;
      .overlay {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        // Uncomment to change it to gradient overlay
        // background: $gradient-transparency;
        // opacity: 0.5;
        background: $overlay-color;
        opacity: 0.12;
      }
    }
  }
  // Carousel controls
  &-control {
    &-prev-icon,
    &-next-icon {
      height: 60px;
      width: 60px;
      // outline: black;
      // background-size: 100%, 100%;
      // border-radius: 50%;
      // border: 1px solid black;
    }
    &-prev-icon {
      background-image: url("../../assets/images/arrow-left.png");
    }
    &-next-icon {
      background-image: url("../../assets/images/arrow-right.png");
    }
  }
}

@keyframes cameraAngleChange {
  to {
    background-position: center center;
  }
}

// Blind effect animation
.blindEffect {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  opacity: 1;
  z-index: 2;
  // -moz-animation: showHide 2500ms ease-in alternate infinite; /* Firefox */
  // -webkit-animation: showHide 2500ms ease-in alternate infinite; /* Safari and Chrome */
  // -ms-animation: showHide 2500ms ease-in alternate infinite; /* IE10 */
  // -o-animation: showHide 2500ms ease-in alternate infinite; /* Opera */
  background: $background-color;
  animation: showHide 1800ms forwards 1200ms ease-in;

  .blindDecoration {
    position: absolute;
    top: 90%;
    left: 0;
    // Drawing triangle
    width: 0;
    height: 0;
    border-top: 50px solid transparent;
    border-left: 1200px solid #1c1c1c;
    border-bottom: 800px solid transparent;
  }
}

@keyframes showHide {
  0% {
    width: 100%;
  }
  100% {
    width: 0%;
  }
}
