@import "./scss//variables";

// Roboto, Oswald, ZCOOL
@import url("https://fonts.googleapis.com/css?family=Oswald|Roboto+Slab|ZCOOL+QingKe+HuangYou&display=swap");

/* Utilities */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  // font-family: "Open Sans Condensed";
  // font-family: "Roboto", sans-serif;
  // font-family: "Cardo", serif;
  // font-family: 'Gelasio', serif;
  font-family: "ZCOOL QingKe HuangYou", cursive;
  letter-spacing: 2px;
  background: $background-color;
}
.modal-open {
  overflow: hidden;
}

a {
  text-decoration: none;
  color: $primary-color;
}

a:hover,
a:visited,
a:active {
  text-decoration: none;
}

span {
  color: $primary-color;
}

.container {
  max-width: $container-max-width;
  margin: auto;
  overflow: hidden;
}

.center {
  text-align: center;
}

// Leading text
.leading {
  color: $primary-color;
  text-transform: uppercase;
  font-family: "Oswald", sans-serif;
  letter-spacing: 5px;
  font-size: 3.5rem;
}

// Normal text
.text {
  color: $primary-color;
  font-size: 1.4rem;
}

.btn {
  color: $primary-color;
  &:hover {
    color: $secondary-color;
  }

  &-custom {
    margin-top: 1rem;
    padding: 0.7rem 1rem;
    letter-spacing: 2px;
    border: 1px $primary-color solid;
    align-self: center;
    background: $background-color;

    &:hover {
      background: $primary-color;
      color: $dark-color;
    }
  }

  &-edit {
    display: inline-block;
    color: $yellow-color;
    border: 1px $primary-color solid;
    margin: 0 0.3rem;
  }
}

button:focus {
  outline: none !important;
  box-shadow: none !important;
}

/* Main */
#root {
  background: #111;
}

.App {
  position: relative;
  // overflow: hidden;
  // min-height: 100vh;
}

.main-container {
  // Grid: Header(sidebar) and other pages(components)
  position: relative;
  z-index: 1;

  display: grid;
  grid-template-columns: $header-width auto;
  grid-template-rows: 100%;

  // Limit main container width
  max-width: $container-max-width;
  margin: auto;
  // margin: 0;
  overflow: hidden;
}

@import "./scss/media";