@import "../../scss/variables";

.order-item {
  font-size: 2rem;
  padding: 1rem;
  // background: $primary-color;

  .card-order {
    margin: 0.5rem 0;
    width: 250px;
    padding: 1rem;
    background: $primary-color;
    box-shadow: $card-box-shadow;
    &:hover {
      transition: all 300ms;
      // background: #ebebeb;
      opacity: 0.6;
      transform: translateY(-1px);
    }
  }
}

#eatstreet {
  font-family: "Roboto Slab", serif;
  font-weight: bold;
  a {
    color: rgb(59, 103, 59);
  }
  span {
    color: #0bab2b;
  }
}
#grubhub {
  a {
    font-family: "Oswald", sans-serif;
    color: rgb(244, 97, 97);
    font-weight: bold;
  }
}
#ubereats {
  font-family: sans-serif;
  letter-spacing: -1px;
  // font-weight: bold;
  a {
    color: #000;
  }
  span {
    color: #0bab2b;
  }
}
