@import "../../scss/variables";

.card-container {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 1rem;
  color: $primary-color;

  // border-radius: 5px;
  // background: $dark-color;
  // box-shadow: $card-box-shadow;
  border: 1px rgba(255, 255, 255, 0.297) solid;
  border-radius: 6px;

  .card-image {
    height: 180px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    // border-left: 2px solid $secondary-color;
    border-radius: 10px;
  }
  .card-text {
    padding: 1rem;
    .card-name-price {
      display: flex;
      justify-content: space-between;
      margin: 0.5rem 0;
    }
    
  }
  .special {
    position: absolute;
    top: -10px;
    left: -17px;
    background: $background-color;
    border-radius: 7px;
    padding: 0 0.2rem;
    transform: rotate(320deg);
    color: $yellow-color;
    // Gradient text; webkit only
    // background: $font-gradient-yellow;
    // -webkit-background-clip: text;
    // -webkit-text-fill-color: transparent;
  }
  .top-menu {
    position: absolute;
    top: 2px;
    right: 10px;
    color: rgb(244, 93, 93);

    .icon-star {
      margin: 0 0.3rem;
    }
  }
}
