@import "../../scss/variables";

$icon-size: 20px;

// Prevents displaying overflow of main slide while transitioning out
.announcement-container {
  background: $background-color;
}
.announcement {
  // Uncomment to overlap with main container
  // position: absolute;
  //   width: 100%;
  position: relative;
  z-index: 2;
  // background: $dark-color;
  background: $secondary-color;
  //   background: rgb(249, 109, 99);
  transition: transform 3s ease-in;

  .text-edit {
    .noodle-icon {
      width: $icon-size;
      height: $icon-size;
      margin: 0 0.3rem;
    }
  }
}
.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 0.5rem;
}

.custom-transition {
  z-index: 2;
  // Uncomment to make it non-fixed
  // position: relative;
  // Uncomment to make it fixed on top
  position: fixed;
  width: 100%;
  top: 0;
}
