@import "../../scss/variables";

.footer {
  // Fixed at the bottom
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 1;
  &-container {
    padding: 0.5rem;
    color: $primary-color;
    background: $background-color;
    text-transform: uppercase;
    text-align: center;
    border-top: 1.5px $sub-color solid;
  }
}
