$primary-color: #fff;
// $secondary-color: #f96d63;
// $secondary-color: #ffcf4d;
// $secondary-color: #be89c1;
$secondary-color: #c23939;
$sub-color: grey;
$background-color: #111;
$dark-color: #333;
$yellow-color: #ffcf4d;

// Header
$logo-size: 220px;


// Heights
// $max-height: calc(100vh - 54px);
$max-height: 100vh;
$page-img-height: 35vh;
$map-height: 600px;

$font-gradient-yellow: -webkit-linear-gradient(rgb(233, 10, 10), rgb(254, 245, 4));
$gradient-transparency: radial-gradient(
  circle at 53%,
  rgba(255, 255, 255, 0),
  rgba(0, 0, 0, 0.694)
);
$gradient-transparency-darker: radial-gradient(
  circle at 53%,
  rgba(255, 255, 255, 0),
  rgb(0, 0, 0)
);
$overlay-color: #000;

// Main container
$container-max-width: 1400px;
$header-width: 250px;

// Box shadow
$card-box-shadow: 0 2px 8px rgb(255, 255, 255);

// Media
$logo-size-mobile: 128px;