@media (max-width: 980px) {
  .menu-item-container {
    grid-template-columns: 1fr;
  }
}

// Tablets & Smaller
@media (max-width: 768px) {
  // Main grid. Main container takes up the whole row
  .main-container {
    grid-template-columns: 1fr;
  }

  // Header
  .header-mobile {
    // Decrease logo size
    .header-logo img {
      width: $logo-size-mobile;
      height: $logo-size-mobile;
    }
  }

  // Announcement bar unfixed
  .custom-transition {
    position: relative;
  }
  // Less announcement padding
  .announcement {
    .container {
      padding: 0.2rem;
    }
  }

  // Stick announcement bar, mobile header together
  .stick-top {
    position: sticky;
    width: 100%;
    top: 0px;
    z-index: 2;
  }

  // Adjust main image
  .main-slide-img {
    // Adjust height: 100% - mobile header - footer - announcement
    height: calc(100vh - 152px - 41.5px - 44.38px) !important;
  }

  // Menu
  // Display tabs in column
  .menu-tab-list {
    // border-bottom: #fff solid 1px;
    margin-bottom: 2rem;
    flex-direction: column;
    align-items: center;
  }
  // Remove top padding
  .tabs {
    padding-top: 0;
  }

  // Give a:active on hamburger menu
  .hamburger-menu-wrap {
    & .header-options {
      a:active {
        outline: solid;
        outline-color: rgb(178, 210, 237);
        outline-width: 1px;
      }
    }
  }

  // Subscribe--reduce margin to reduce height on mobile
  .subscribe {
    h1.leading {
      margin-bottom: 0.7rem;
    }
    p.text {
      margin-bottom: 0;
    }
    &-form {
      button.btn-custom {
        margin: 0;
      }
    }
  }
  // Move Modal's close button to the corner
  .modal-show .btn-close-2 {
    top: 0;
    right: 0;
  }

  // Contact page
  .contact-page {
    padding: 0;
    margin: auto;
  }
  .contact {
    padding: 1rem;
    .leading{
      margin-top: 1rem;
    }
  }
  // Smaller heading
  .leading {
    font-size: 2rem !important;
    letter-spacing: 3px;
    margin-bottom: 1rem !important;
  }
  .text {
    font-size: 1.1rem;
    margin-bottom: 0;
  }
}

// Short devices
@media (max-height: 610px) {
  .subscribe p.text {
    display: none;
  }
}

// Mobile phones
@media (max-width: 482px) {
  // Menu item
  .card-container {
    // Show food image, description vertically
    grid-template-columns: 1fr;

    // Reposition Top Menu
    .top-menu {
      background: $background-color;
      top: -12px;
      right: -10px;
    }
  }
}
