@import "../../scss/variables";

$toggler-size: 50px;
$hamburger-size: 60px;
$hamburger-line-thickness: 3px;
$menu-speed: 750ms;

.hamburger-menu-wrap {
  position: absolute;
  top: 5px;
  right: 0;
  z-index: 4;

  // Move down when scrolled
  &--reposition {
    transition: top 300ms ease-in;
    top: 35px;
    
  }

  .toggler {
    // Position checkbox
    position: absolute;
    top: 0;
    right: 0;
    z-index: 5;
    cursor: pointer;
    width: $toggler-size;
    height: $toggler-size;

    // Hide
    opacity: 0;

    // Behavior when toggler is checked
    &:checked {
      // Toggler animation when checked
      & + .hamburger > div {
        transform: rotate(135deg);

        &:before,
        &:after {
          top: 0;
          transform: rotate(90deg);
        }
      }

      // Rotate on hover when checked
      &:hover + .hamburger > div {
        transform: rotate(225deg);
      }

      // Make menu visible
      & ~ .hamburger-menu {
        visibility: visible;

        // Bring in the dark overlay
        & > div {
          transform: scale(1.3);
          transition-duration: $menu-speed;
          // Bring in the a tags
          & > div {
            opacity: 1;
            // Opening speed
            transition: opacity 200ms ease 200ms;
          }
        }
      }
    }
  }

  .hamburger {
    // Hamburger wrapper
    position: absolute;
    top: 0;
    right: 0;
    z-index: 4;
    width: $hamburger-size;
    height: $hamburger-size;
    padding: 1rem;
    background: none;

    display: flex;
    justify-content: center;
    align-items: center;

    // Draw a mid line
    &-lines {
      position: relative;
      flex: none;
      width: 100%;
      height: $hamburger-line-thickness;
      background: $primary-color;

      display: flex;
      justify-content: center;
      align-items: center;

      transition: all 400ms ease-in;

      // Draw top and bot lines
      &:before,
      &:after {
        content: "";
        position: absolute;
        z-index: 4;
        top: -10px;
        width: 100%;
        height: $hamburger-line-thickness;
        background: inherit; // Inherit primary color
      }
      // Move down a line
      &:after {
        top: 10px;
      }
    }
  }

  // Overlay menu
  .hamburger-menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    visibility: hidden;
    overflow: hidden;
    display: flex;

    align-items: center;
    justify-content: center;
    // Dark background overlay
    & > div {
      background: $background-color;
      width: 200vw;
      height: 200vw;
      display: flex;
      align-items: center;
      justify-content: center;

      // Transition to close the menu
      // transform: scale(0);
      // transition: all 200ms ease;
      // border-radius: 10%;
    }

    // Menu items
    & > div > div {
      text-align: center;
      max-width: 90vw;
      max-height: 100vh;

      // Close
      opacity: 0;
      // Closing speed of a link
      transition: opacity 400ms ease;
    }

    & > div > div > ul > li {
      list-style: none;
      color: $primary-color;
      font-size: 1.5rem;
      padding: 1rem;
      & > a {
        color: inherit;
        text-decoration: none;
        transition: color 400ms ease;
        &:hover {
          color: $secondary-color;
        }
      }
    }
  }
}
