@import "../../scss/variables";

.modal {
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  //   background: #000;
  background: rgba(18, 18, 18, 0.99);
  animation: animateModal 200ms ease-in;
  margin: auto;

  .btn-close-2 {
    position: absolute;
    top: 1rem;
    right: 1rem;
    font-size: 1.5rem;
    z-index: 7;
  }
}
.modal-show {
  @extend .modal;
  display: block;
}
.modal-hide {
  @extend .modal;
  display: none;
}
.modal-main {
  visibility: hidden;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 6;
  // Uncomment to distinguish the inner container
  //   background: rgba(120, 120, 120, 0.315);
  height: auto;
  padding: 1rem;
  animation: delayIn 400ms forwards 500ms ease-in;
}

@keyframes animateModal {
  from {
    // opacity: 0;
    border-radius: 50%;
    transform: scale(0.2);
  }
  to {
    border-radius: 0;
    transform: scale(1.5);
  }
}

@keyframes delayIn {
  from {
    opacity: 0;
    // transform: translate(-1000px, -50%);
  }
  to {
    visibility: visible;
    opacity: 1;
    // transform: translate(-50%, -50%);
  }
}
