splay:flex;align-items:center;justify-content:center}.hamburger-menu-wrap .hamburger-menu>div>div{text-align:center;max-width:90vw;max-height:100vh;opacity:0;-webkit-transition:opacity 400ms ease;transition:opacity 400ms ease}.hamburger-menu-wrap .hamburger-menu>div>div>ul>li{list-style:none;color:#fff;font-size:1.5rem;padding:1rem}.hamburger-menu-wrap .hamburger-menu>div>div>ul>li>a{color:inherit;text-decoration:none;-webkit-transition:color 400ms ease;transition:color 400ms ease}.hamburger-menu-wrap .hamburger-menu>div>div>ul>li>a:hover{color:#c23939}

.header-mobile-logo-hamburger{position:relative;display:flex;justify-content:space-between;padding:0 0.5rem;background:#111;z-index:4}.header-mobile-address{padding:0 1rem;position:relative;z-index:1;-webkit-transition:-webkit-transform 600ms;transition:-webkit-transform 600ms;transition:transform 600ms;transition:transform 600ms, -webkit-transform 600ms}.header-mobile-address--hidden{-webkit-transform:translateY(-50px);transform:translateY(-50px)}

.main-slide{position:relative;overflow:hidden}.carousel-item .main-slide-img{width:100%;height:100vh;position:relative;background-repeat:no-repeat;background-position:50% center;background-size:cover}.carousel-item .main-slide-img .overlay{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:#000;opacity:0.12}.carousel-control-prev-icon,.carousel-control-next-icon{height:60px;width:60px}.carousel-control-prev-icon{background-image:url(data:image/png;base64,)}.carousel-control-next-icon{background-image:url(data:image/png;base64,)}@-webkit-keyframes cameraAngleChange{to{background-position:center center}}@keyframes cameraAngleChange{to{background-position:center center}}.blindEffect{position:absolute;top:0;bottom:0;right:0;width:100%;opacity:1;z-index:2;background:#111;-webkit-animation:showHide 1800ms forwards 1200ms ease-in;animation:showHide 1800ms forwards 1200ms ease-in}.blindEffect .blindDecoration{position:absolute;top:90%;left:0;width:0;height:0;border-top:50px solid transparent;border-left:1200px solid #1c1c1c;border-bottom:800px solid transparent}@-webkit-keyframes showHide{0%{width:100%}100%{width:0%}}@keyframes showHide{0%{width:100%}100%{width:0%}}

#section-image{margin-bottom:1rem}.section-img{height:35vh;background-repeat:no-repeat;background-position:center center;background-size:cover;position:relative}.section-img .overlay{content:"";position:absolute;top:0;left:0;width:100%;height:100%}.section-img .overlay-light{background:radial-gradient(circle at 53%, rgba(255,255,255,0), rgba(0,0,0,0.694));opacity:0.5}.section-img .overlay-dark{background:#000;background:#000;opacity:0.3}.overlay-text{position:absolute;top:50%;left:50%;-webkit-transform:translateX(-50%) translateY(-50%);transform:translateX(-50%) translateY(-50%)}

.location-page{display:block}#location-text{display:flex;flex-direction:column;align-items:center;margin:0 auto;padding:2rem}#location-text .location-text{font-size:1.2rem}#location-text .location-text span{display:block}#location-text .location-text a,#location-text .location-text a:hover,#location-text .location-text a:visited,#location-text .location-text a:active{text-decoration:underline}#location-text .location-text a:link,#location-text .location-text a:visited{color:#fff}#location-text .location-text a:hover{color:#c23939}#location-map{position:relative;width:100%;height:600px}

.mapContainer{position:absolute;top:0;right:0;left:0;bottom:0}.sidebarStyle{display:inline-block;position:absolute;top:0;left:0;margin:12px;background-color:#404040;color:#ffffff;z-index:1 !important;padding:6px;font-weight:bold}.get-direction{position:absolute;top:5%;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);z-index:1;background:#111;text-transform:uppercase;padding:1rem}.mapboxgl-map{font-family:inherit}

.card-container{position:relative;display:grid;grid-template-columns:1fr 1fr;padding:1rem;color:#fff;border:1px rgba(255,255,255,0.297) solid;border-radius:6px}.card-container .card-image{height:180px;background-repeat:no-repeat;background-position:center center;background-size:cover;border-radius:10px}.card-container .card-text{padding:1rem}.card-container .card-text .card-name-price{display:flex;justify-content:space-between;margin:0.5rem 0}.card-container .special{position:absolute;top:-10px;left:-17px;background:#111;border-radius:7px;padding:0 0.2rem;-webkit-transform:rotate(320deg);transform:rotate(320deg);color:#ffcf4d}.card-container .top-menu{position:absolute;top:2px;right:10px;color:#f45d5d}.card-container .top-menu .icon-star{margin:0 0.3rem}

.menu-tab-list{display:flex;justify-content:center;padding-left:0;text-transform:uppercase}.menu-tab-list-item{display:inline-block;list-style:none;margin:0 1rem;margin-bottom:-1px;padding:0.5rem 0.75rem;color:#8e8e8e;position:relative;cursor:pointer}.menu-tab-list-item:hover{-webkit-transition:color 300ms;transition:color 300ms;color:#fff}.menu-tab-list-active{color:#fff;border-bottom:2px solid #fff}

.tabs{padding:2rem}.tabs .menu-tab-list{display:flex;flex-wrap:wrap}

.menu-item-container{display:grid;grid-template-columns:1fr 1fr;grid-gap:2rem;gap:2rem}

