.contact{display:flex;flex-direction:column;align-items:center;padding:2rem;min-width:330px}.contact h1{font-size:3rem;margin-bottom:2rem}.contact-form{width:90%}

.contact-page{padding:5rem}

.order-item{font-size:2rem;padding:1rem}.order-item .card-order{margin:0.5rem 0;width:250px;padding:1rem;background:#fff;box-shadow:0 2px 8px #fff}.order-item .card-order:hover{-webkit-transition:all 300ms;transition:all 300ms;opacity:0.6;-webkit-transform:translateY(-1px);transform:translateY(-1px)}#eatstreet{font-family:"Roboto Slab", serif;font-weight:bold}#eatstreet a{color:#3b673b}#eatstreet span{color:#0bab2b}#grubhub a{font-family:"Oswald", sans-serif;color:#f46161;font-weight:bold}#ubereats{font-family:sans-serif;letter-spacing:-1px}#ubereats a{color:#000}#ubereats span{color:#0bab2b}

