nt}.text{font-size:1.1rem;margin-bottom:0}}@media (max-height: 610px){.subscribe p.text{display:none}}@media (max-width: 482px){.card-container{grid-template-columns:1fr}.card-container .top-menu{background:#111;top:-12px;right:-10px}}

